import React, { PureComponent } from 'react';
import { Modal, Steps, Button, message } from 'antd';
import EditPerTahap from './edit-per-tahap';
import "./info-project.less";

const Step = Steps.Step;

const steps = [{
    title: 'Pre Screening',
    content: <EditPerTahap />
  }, {  
    title: 'Pipeline',
    content: 'Pipeline',
  }, {
    title: 'Siap Kerja (Mandat)',
    content: 'Siap Kerja (Mandat)',
  }, {
    title: 'Implementasi',
    content: 'Implementasi',
  }, {
    title: 'Closing',
    content: 'Closing',
  }
];
  

class InfoProject extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
          current: 0,
        };
    }

    next() {
        const current = this.state.current + 1;
        this.setState({ current });
      }
    
      prev() {
        const current = this.state.current - 1;
        this.setState({ current });
      }

    render() {
        const { current } = this.state;
        const {onDone, record, appContext, ...otherProps } = this.props;
        return(
            <Modal 
                footer={[
                    <Button key="OK" type="primary" onClick={this.props.onCancel} disabled>
                      Edit
                    </Button>,
                ]}
                {...otherProps}>
                <Steps current={current}>
                  {steps.map(item => <Step key={item.title} title={item.title} />)}
                </Steps>
                <div className="steps-content">
                  {/* {steps[current].content} */}
                  <EditPerTahap record={record} appContext={appContext} />
                </div>
                <div className="steps-action">
                {
                    current < steps.length - 1
                    && <Button type="primary" onClick={() => this.next()}>Next</Button>
                }
                {
                    current === steps.length - 1
                    && <Button type="primary" onClick={onDone}>Done</Button>
                }
                {
                    current > 0
                    && (
                    <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
                    Previous
                    </Button>
                    )
                }
                </div>
            </Modal>
        );
    }
}

export default InfoProject;